$(function () {
    $('.vacancy-form__block').on('submit', function (e) {
        //-    https://learn.javascript.ru/forms-submit
        e.preventDefault();
        //-    
        var $this = $(this);

        //-  ------------------------------


        //-              email
        var name = $this.find('input[name=name]').val();
        var phone = $this.find('input[name=phone]').val();
        var email = $this.find('input[name=email]').val();
        var text = $this.find('input[name=message]').val();
        var namePar = $this.find('input[name=name]').parent();
        var phonePar = $this.find('input[name=phone]').parent();
        var emailPar = $this.find('input[name=email]').parent();
        var textPar = $this.find('input[name=message]').parent();
        var pattern = /^[a-z0-9_-]+@[a-z0-9-]+\.([a-z]{1,6}\.)?[a-z]{2,6}$/i;
        var successEmail = false;


        //-    
        if (name.length !== 0) {
            //-    ( )
            namePar.removeClass('wrong');
        } else {
            //-   ( )
            namePar.addClass('wrong');
        };

        //-    
        if (phone.length !== 0) {
            //-    ( )
            phonePar.removeClass('wrong');
        } else {
            //-   ( )
            phonePar.addClass('wrong');
        }

        //-    
        if (email.length !== 0) {
            //-    ( )
            emailPar.removeClass('wrong');

            //-     
            if (email.search(pattern) == 0) {
                //-    (      )
                emailPar.removeClass('wrong');
                successEmail = true;
            } else {
                //-   (       )
                emailPar.addClass('wrong');
            }

        } else {
            //-   ( )
            emailPar.addClass('wrong');
        }


        //-    
        if (text.length !== 0) {
            //-    ( )
            textPar.removeClass('wrong');
        } else {
            //-   ( )
            textPar.addClass('wrong');
        }

        //-       
        if ($this.find('.wrong').length == 0) {
            //-   ----------------------------------------------

            $this.trigger('precapcha');
        }
    });

    $('.vacancy-form__block').on('postcapcha', function (e) {
        var $this = $(this);
        //console.log('submit');
        //feedbackThanks.openModal();
        //feedbackModal.closeModal();


        var fd = new FormData(this);
        //-  ajax    
        $.ajax({
            url: $this.attr('action'), //     
            type: $this.attr('method'), //   
            data: fd, //      https://ruseller.com/jquery?id=8
            processData: false,
            contentType: false,
            success: function () { //       
                //-    
                $this[0].reset();
                console.debug('vacancy sended');
                //feedbackThanks.openModal();
                //feedbackModal.closeModal();
            }
        });
        $this.trigger('afterrequest');
    });
})

