$(document).ready(function () {
    const todesMain = $(".todes-main");
    if (todesMain) {
        let todesSwiper = new Swiper(".todes-main__slider ", {
            slidesPerView: 1,
            spaceBetween: 160,
            navigation: {
                nextEl: ".todes-main__slider-arrow--next",
                prevEl: ".todes-main__slider-arrow--prev",
            },
            pagination: {
                el: ".todes-main__slider-pagination",
                clickable: true,
            },
        });
    }

    $(".todes-main__section").each(function () {
        let num = $(this).find(".todes-main__num-val").height();
        $(this).css("min-height", num + "px");
    });

    $(window).on("resize", function () {
        $(".todes-main__section").each(function () {
            let num = $(this).find(".todes-main__num-val").height();
            $(this).css("min-height", num + "px");
        });
    });
});
