var swiper = new Swiper(".swiper", {
  cssMode: true,
  slidesPerView: 2,
  spaceBetween: 8,
  navigation: {
    nextEl: ".swiper-button-next",
    prevEl: ".swiper-button-prev",
  },

  mousewheel: true,
  keyboard: true,
});
