        let projectsSwiper = new Swiper(".projects__swiper", {
            direction: "vertical",
            slidesPerView: 1,
            mousewheel: true,
            speed: 1100,
            observer: true,
            observeSlideChildren: true,
        });

        projectsSwiper.on("slideChange", function () {
            let slides = $(".projects__item");
            let activeIndex = projectsSwiper.activeIndex;

            // 
            slides.removeClass("projects__item--active");
            slides.removeClass("projects__item--first");
            slides.removeClass("projects__item--hide");
            slides.eq(activeIndex).addClass("projects__item--active");
            slides.eq(activeIndex - 1).addClass("projects__item--hide");

            //

            $(".projects__menu-item").removeClass(
                "projects__menu-item--active"
            );

            $(".projects__menu-item")
                .eq(activeIndex)
                .addClass("projects__menu-item--active");

            // $(".projects__menu-wrapper-items").css("top", 0);
            $(".projects__menu-wrapper").scrollTop(0);

            let menu = $(".projects__menu");

            let menuItemActive = menu.find(".projects__menu-item--active");

            let $parent = menuItemActive.parent(),
                elementTop = menuItemActive.offset().top,
                parentTop = $parent.offset().top,
                distance = elementTop - parentTop;

            $(".projects__menu-wrapper-items").css(
                "top",
                buttonPos - activeMenuPos - distance + 20 + "px"
            );

            console.log(
                "  -".buttonPos,
                "   -",
                activeMenuPos,
                " - ",
                distance
            );
            //  
            let mainInfoSlideH = $(".projects__main-info-slide")
                .eq(activeIndex)
                .innerHeight();

            let infoElements = $(".projects__main-info-slide");
            let infoH = 0;
            for (let i = 0; i < activeIndex; i++) {
                infoH += Math.round(infoElements[i].clientHeight);
            }

            $(".projects__main-info-wrapper").css("top", -infoH + "px");
            $(".projects__main-info").css("max-height", mainInfoSlideH + "px");

            // 
            let numSlideH = $(".projects__pagination-num-item")
                .eq(activeIndex)
                .innerHeight();

            let numElements = $(".projects__pagination-num-item");
            let numH = 0;
            for (let i = 0; i < activeIndex; i++) {
                numH += Math.round(numElements[i].clientHeight);
            }

            $(".projects__pagination-num-holder").css("top", -numH + "px");
            $(".projects__pagination-num").css("max-height", numSlideH + "px");

            // 
            let partnersSlideH = $(".projects__partners-slide")
                .eq(activeIndex)
                .innerHeight();

            let partnersElements = $(".projects__partners-slide");
            let partnersH = 0;
            for (let i = 0; i < activeIndex; i++) {
                partnersH += Math.round(partnersElements[i].clientHeight);
            }

            $(".projects__partners-wrapper").css("top", -partnersH + "px");
            $(".projects__partners").css("max-height", partnersSlideH + "px");
        });

