function checkDomainInput (input) {
    var domainPattern =
        /^(http(s)?:\/\/)?([\w-]+\.)*([\w-]+\.[a-zA-Z]{2,63})(\/[\w-./?%&=]*)?$/

    if (input.value === '') {
        input.style.border = '' //    
    } else if (domainPattern.test(input.value)) {
        input.style.border = '2px solid #A8E300' //      
    } else {
        input.style.border = '2px solid #FF8F70' //      
    }
}

function checkInput (input) {
    if (input.value !== '') {
        input.style.opacity = '1'
    } else {
        input.style.opacity = '0.4'
    }
}
